@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');
*, *::before, *::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    /* max-width: 100%; */
    font-family: Roboto;
}

.welcome{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: black;
    width: 100vw;
    height: 100vh;
    min-width:360px;
    min-height: 650px;
    gap: 40px;
    padding: 5rem;
}

.start{
    width: 100px;
    height: 50px;
    font-size: 1rem;
    border-radius: 10px;
    border: none;
    opacity: 0.8;
    background-color: white;
    font-weight: 500;
}

.start:hover{
    opacity: 1;
}

.welcomeImg{
    width: 20vw;
}

.imgTable{
    display: grid;
    height: 25vw;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    justify-content: center;
    align-items: center;
    gap: 30px;
}

.welcomeName{
    justify-self: center;
}

@media(max-width: 520px){
    .welcome{
        height: auto;
    }

    .imgTable{
        height: auto;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
    }

    .welcomeName{
        display: none;
    }

    .welcomeImg{
        width: 15rem;
    }
}